.hero-section {
  margin: 5% auto;
  /* padding: 0% 0 !important; */
  display: flex;
  justify-content: space-between;
  align-items: center !important;
  width: 80%;
}
.hero-section .details .badges {
  /* display: flex;
  justify-content: flex-start;
  align-items: center; */
  /* width: 50%; */
  margin: 10px 0;
}
/* .badge-container{
  margin: ;
} */
.hero-section .details .badges .badge {
  padding: 10px;
  background: #09bfad28;
  color: #09bfad;
  width: 100% !important  ;
  margin: 10px 0;
  word-break: break-all;
}
.hero-section .details .badges .badge:nth-child(2),
.hero-section .details .badges .badge:nth-child(3) {
  margin-left: 10px;
}
.hero-section .details .badges .badge p {
  font-size: 0.7rem !important;
  margin: 0;
  line-height: normal !important;
  font-weight: 600;
}
.tag p {
  text-align: center;
}
.hero-section .details .badges .badge p strong {
  color: #09bfad;
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
}

.hero-section .video-container {
  width: 50%;
  position: relative;
  height: 500px;
  background: #09bfad;
  border-radius: 0 0 10px 10px;
}
.hero-section .video-container iframe {
  position: absolute;
  bottom: -10%;
  left: 5%;
  /* transform: translate3d(-50%, -50%, 0); */
  width: 90%;
  height: 100%;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  z-index: 2;
  background: black;
}
.hero-section .details {
  width: 50%;
}
.hero-section .details .content {
  margin: 35px 0;
}
.hero-section .details .content p {
  font-size: 0.9rem;
  color: rgba(0, 0, 0, 0.7);
  width: 70%;
  line-height: 2rem !important;
}
.hero-section .details .buy-now-btn {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.hero-section .details .buy-now-btn h4 {
  font-size: 1.3rem;
  margin: 0 0 0 20px;
  font-weight: 800;
}
.hero-section .details .buy-now-btn h4 del {
  font-size: 0.7rem;
  font-weight: 400 !important;
  font-family: "Montserrat", sans-serif;
}
.hero-section .details .header h6 {
  font-size: 0.8rem;
  color: rgba(0, 0, 0, 0.5);
  text-transform: capitalize !important;
  margin-bottom: 10px;
}
.hero-section .details .header h1 {
  font-size: 2rem;
  font-weight: 700;
  max-width: 90%;
}
.round-btn {
  padding: 25px 15px !important;
  box-shadow: none !important;
}

/*Features Section*/

.features-section-container {
  width: 80% !important;
  margin: auto !important;
  padding: 100px 0;
}
/* .features-section {
  justify-content: space-between !important;
} */
.features-section .image-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 50%;
  position: relative;
}
.features-section .image-container .girl-real {
  width: 550px;
  height: 550px;
  background-size: cover;
  background-position: 10% 0;
  background-repeat: no-repeat;
  border-radius: 50%;
  position: relative;
}

.features-section .image-container .child-cartoon {
  width: 350px;
  height: 350px;
  position: absolute;
  right: -15%;
  bottom: -10%;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 50%;
  background-position: center;
  border: 20px solid white;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}
.features-section .features {
  display: flex;
  justify-content: center;
  align-items: center;
}
.features-section .features .feature-card-container {
  padding: 20px;
}
.features-section .features .feature-card {
  border: 1px solid rgba(0, 0, 0, 0.07);
  border-radius: 10px;
  padding: 30px;
  height: 100%;
}
.features-section .features .feature-card h5 {
  background: #09bfad;
  color: white;
  font-weight: 600;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px 10px;
  font-size: 0.9rem;
  box-shadow: -5px -5px 0 rgba(0, 0, 0, 0.7);
}
.features-section .features .feature-card h3 {
  color: #09bfad;
  font-size: 2rem;
  font-weight: 800;
  margin-top: 20px;
}
.features-section .features .feature-card p {
  color: rgba(0, 0, 0, 0.5);
}
.checkout-btn {
  border: none !important;
  outline: none !important;
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  padding: 25px !important;
}
/*Features End*/

/*What You get*/
.tags,
.games-container,
.games,
.head-games,
.tag,
.what-you-get {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  width: 100%;
}
.what-you-get-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background: #f0fbfa;
}
.what-you-get {
  flex-direction: column;
  padding: 100px 0;
  width: var(--fixedWidth);
}
.games-container {
  flex-direction: column;
}
.tags {
  margin: 10px 0;
  width: 100%;
  /* padding: 0 0; */
}
.tag {
  background: #c9f1ec;
  padding: 10px 15px;
  color: #028172;
  border-radius: 5px;
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  height: 50px;
  /* margin: 0 10px; */
}
.tag {
  margin: 0 10px;
  width: 90% !important;
}
.tag p {
  margin: 0;
  font-weight: 800;
  line-height: normal !important;
  width: 100%;
}
.games-container {
  background: white;
  margin: 20px 0;
  padding: 50px;
  border-radius: 10px;
  box-shadow: 5px 5px 40px #c9f1ec;
}
.head-games h4 {
  font-weight: 700;
  color: black;
  font-size: 1rem;
}
.games {
  padding: 20px 0;
}
.game {
  height: 200px;
  width: 200px;
}
.game p {
  /* height: 50px; */
  margin-top: 15px;
  font-size: 0.9rem;
  color: rgba(0, 0, 0, 0.6);
}
.game-image-container {
  width: 100%;
  height: 150px;
  overflow: hidden;
}
.game-image-container img {
  width: 100%;
  height: 100%;
  border-radius: 5px 5px 0 0;
  object-fit: cover;
}

.know-more-btn {
  color: rgba(0, 0, 0, 0.9);
  font-family: "Montserrat", sans-serif !important;
  text-transform: capitalize !important;
  background: transparent !important;
  position: relative;
  outline: none !important;
  border: none !important;
  margin-right: 20px;
}

.know-more-btn img {
  position: absolute;
  width: 15px;
  margin-left: 10px;
  transition: all 0.2s ease-in-out;
  right: -15px;
  top: 50%;
  transform: translate(0, -50%);
}

.know-more-btn:hover img {
  right: -18px;
}

.grass-section {
  padding: 100px 0;
  width: 100%;
}

.grass-section .grass-with-boy {
  height: 700px;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
}

.what-you-get-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  background: #def8f5;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 50px;
  margin-left: 10rem;
  border-radius: 10px;
}
.what-you-get-card h3 {
  font-size: 2rem;
  color: #028172;
  text-transform: uppercase;
  font-weight: 800;
}
.what-you-get ul {
  /* padding: 20px; */
  margin: 0;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}
.what-you-get ul li {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 25px 0;
  color: #028172;
  font-size: 1.3rem;
  font-weight: 400;
}
.what-you-get ul li img {
  width: 25px;
  margin-right: 20px;
}
