    width: 250px;
    height: 250px;
  }
  .features-section .image-container {
    background-position: 65% 15%, 4% 85% !important;
  }
  /*What You Get*/

  .tag p {
    font-size: 0.8rem;
  }

  .what-you-get-card {
    margin-left: 2rem;
  }
}
