  /*Hero Section*/
  .hero-section {
    flex-direction: column;
    width: 100%;
  }
  .hero-section .details {
    width: 90%;
    padding: 100px 0;
  }
  .hero-section .video-container {
    width: 100vw;
    padding: 20px 0;
  }
  .hero-section .video-container iframe {
    position: relative;
    top: 0;
  }
  .for-mobile {
    display: flex;
  }
  .hero-section .video-container {
    width: 100%;
    border-radius: 10px;
    /* margin: 0;
