  }
  .features-section .image-container .child-cartoon {
    width: 120px;
    height: 120px;
    border-width: 5px;
  }
}

.round-btn {
  border-radius: 25px !important;
  vertical-align: middle;
  justify-content: center;
  background: #06bfad !important;
  border: 0px;
  color: #fff !important;
  font-family: Montserrat-Bold !important;
  box-shadow: 2px 2px 10px 0 #06bfad;
  height: 37px;
  width: 130px;
  text-transform: capitalize !important;
}
.offer {
  font-size: 1.1rem;
  margin: 0 10px;
  font-weight: 800;
  color: var(--accentColor);
}
.image-container {
  background-image: url(/static/media/triangles.cfe9c0af.svg),
    url(/static/media/waves.8c53cf60.svg);
  background-repeat: no-repeat;
  background-position: 70% 10%, 5% 95% !important;
}

